export default {
	data() {
		return {
			isRead: false, //银元宝数，元宝数不够1000时无权访问
			loadMoreText: '数据加载中...',
			showLoadMore: false,
			isMore: true,
			PageNo: 1,
			PageSize: 10,
			mainItem: [],
			keyWords: '' //搜索关键词
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		this.getUserUp();
	},
	onUnload() {
		(this.PageNo = 1), (this.mainItem = []), (this.loadMoreText = '加载更多'), (this.showLoadMore = false);
	},
	onReachBottom() {
		//下拉加载
		this.showLoadMore = true;
		setTimeout(() => {
			this.setListData();
		}, 300);
	},
	onPullDownRefresh() {
		//上拉刷新
		this.initData();
	},
	methods: {
		search(res) {
			this.keyWords = res.value;
			this.initData();
		},
		dumpInfo(id) {
			uni.navigateTo({
				url: '/pages/shop/info?id=' + id
			});
		},
		initData() {
			//刷新数据
			setTimeout(() => {
				this.PageNo = 1;
				this.mainItem = [];

				this.getPage();
				uni.stopPullDownRefresh();
			}, 300);
		},
		setListData() {
			//加载更多数据
			this.PageNo += 1;
			if (this.isMore) this.getPage();
		},
		getPage() {
			//加载数据
			var _this = this;
			if (!_this.isRead) {
				_this.PageNo = 1;
				_this.mainItem = [];
				_this.keyWords = '';
				_this.loadMoreText = '您的银元宝未达到最低进入标准：1000银元宝';
				return false;
			}
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.shop
				.getGoodsList({
					page: _this.PageNo,
					keyWords: _this.keyWords
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 1) {
						_this.mainItem = _this.mainItem.concat(data.data);
						if (data.data.length < _this.PageSize) {
							this.loadMoreText = '没有更多数据了';
							_this.isMore = false;
						} else {
							_this.showLoadMore = false;
							_this.isMore = true;
						}
					} else {
						_this.showLoadMore = false;
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.showLoadMore = false;
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		getUserUp() {
			//获取是否可以升级
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						_this.isRead = data.data.gold_balance >= 1000;
						_this.initData();
						if (data.data.up == 1) {
							uni.setTabBarBadge({
								index: 3,
								text: '1'
							})
						} else {
							uni.setTabBarBadge({
								index: 3
							})
						}
					}
				});
		}
	}
};
